<template>
    <div class="page-container">
        <page-sidebar></page-sidebar>
        <div class="page-content-wrapper">
            <top-nav :pTitle="pageTitle"></top-nav>
            <!-- <div class="container data-list"> -->
                <!-- <router-view @viewMode='setViewMode' @openDet='setCurrent' :tra="curItem"/> -->
            <!-- </div> -->
            <router-view @viewMode='setViewMode' @authorized="setAuthorized" @setPeriod="onSetPeriod"/>
            <page-footer></page-footer>
        </div>
    </div>
</template>

<script>
import PageSidebar from '@/components/Sidebar.vue';
import TopNav from '@/components/TopNav.vue';
import PageFooter from '@/components/Footer.vue';

export default {
    name:'TransactionList',
    data() {
        return {
            pageTitle: '거래내역 조회',
            viewMode:'search',
            //reAuthorized:false,
            reAuthorized: {
                auth:false,
                downEx:false,
                viewDet:false,
                purpose:''
            },
            fromDate:null,
            toDate:null
        }
    },
    components: {
        PageSidebar,
        TopNav,
        PageFooter,
    },
    methods: {
        // 네비게이션 가드로 처리하는 방법도?
        setViewMode(type){
            this.viewMode = type
        },
        setAuthorized(reAuth) {
            console.log('authorization state changed', reAuth.auth, reAuth.purpose)
            this.reAuthorized = reAuth;
        },
        onSetPeriod(date) {
            this.fromDate = date.from
            this.toDate = date.to
        }
    },
    watch:{
        viewMode(newVal) {
            this.pageTitle = newVal=='search' ? '거래내역 조회' : '거래 상세내역';
        }
    },
}
</script>
