<template>
    <nav class="navbar top bg-light">
        <div class="navbar-brand">
            <a href="/" class="navbar-item">{{pTitle}}</a>
            <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
                <span aria-hidden="true"></span>
            </a>
        </div>
        <div id="navbar" class="navbar-menu">
            <div class="navbar-start">
                {{ dispId }}
            </div>
            <div class="navbar-end">
                <a href="" @click="signOut" class="navbar-item"><i class="fas fa-sign-out-alt"></i></a>
            </div>
        </div>
    </nav>
</template>
<script>
export default {
    name: 'TopNav',
    data: function() {
        return {
            dispId:'not logined',
        }
    },
    props: ['pTitle'],
    methods: {
        signOut: function() {
            localStorage.removeItem('instapay_token');
            localStorage.removeItem('instapay_id');
            this.$router.push('/logIn');
            this.$store.commit('setIfBookstore', false)
        }
    },
    mounted() {
        this.dispId = localStorage.getItem('instapay_id');
    }
}
</script>
