//     return axios.request({
//         url,
//         method: 'post',
//         params: {
//             s:'complete',
//             t: period,
//         }
//     })
//}
let btras = (period, toDate, smode, kw) => {
    const url = `${API_PATH}/${BTRAS_PATH}`

    return axios.request({
        url,
        method: 'get',
        params: {
            s:'complete',
            t: period,
            d: smode,
            qb: toDate,
            uname: kw.uname,
            email: kw.email,
            isbn13: kw.isbn13,
            pub:kw.publisher,
            tid:kw.qtid,
        },
        headers: {
            //authorization
            authorization: `Bearer ${token}`
        }
    })
}

let writeLogPurpose = (str, log)=>{
    //window.alert(log.purpose)
    console.log('using privacy info ', log.auth, log.purpose, 'at ', str)
}

export default {
    login,
    tras,
    btras,
    loadToken,
    updateToken,
    writeLogPurpose,
}